
body {
  overflow-y: scroll;
}
#calendar__wrap {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  min-height: 100%;
  text-align: center;
  background: #7474BF;
  background: -webkit-gradient(linear, left bottom, left top, from(#348AC7), to(#7474BF));
  background: linear-gradient(to top, #348AC7, #7474BF);
}
.calendar__inner {
  width: 800px;
  margin: auto;
  text-align: left;
  line-height: 70px;
}
#calendar__head {
  position: absolute;
  top: 40px;
  left: 0px;
  width: 100%;
  text-align: center;
}
#calendar__logo {
  height: 70px;
  background: rgba(255, 255, 255, 0.5);
  padding: 10px;
  border-radius: 15px;
  float: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.logo__text {
  color: #FFF;
  font-family: Montserrat;
  font-weight: 100;
  font-size: 20px;
  letter-spacing: 1px;
  margin-left: 20px;
  float: right;
}
#date__text {
  font-family: Montserrat;
  font-weight: 300;
  font-size: 50px;
  float: left;
  color: #FFF;
}
.date__text--month {
  display: inline-block;
}
.date__text--year {
  display: inline-block;
  font-size: 16px;
  margin-left: 20px;
  margin-top: 10px;
}
#calendar__controls {
  float: left;
  margin-top: -70px;
  margin-left: 20px;
}
.calendar__controlBtn {
  background-color: #FFF;
  border-radius: 5px;
  height: 30px;
  line-height: 40px;
  width: 40px;
  text-align: center;
  display: inline-block;
  margin-right: 10px;
  -webkit-box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
          box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
#calendar__body {
  margin: auto;
  margin-top: 200px;
  width: 910px;
}
.calendar__weekPart {
  margin-bottom: 10px;
  display: inline-block;
  width: 130px;
  color: #FFF;
  font-size: 12px;
}



